import React from 'react';
import {HashRouter, Switch, Route, withRouter, BrowserRouter} from 'react-router-dom';
import './App.css';
import Home from './Home.js';
import Images from './Images.js';
import Videos from './Videos.js';
import Links from './Links.js';
function App() {
  // Github pages doesn't work with react routers so hardcoding urls
  if(window.location.href == "https://ethan-scott.github.io/cs185_react/"){
    return(<Home/>)
  }
  if(window.location.href == "https://ethan-scott.github.io/cs185_react/index"){
    return(<Home/>)
  }
  if(window.location.href == "https://ethan-scott.github.io/cs185_react/images"){
    return(<Images/>)
  }
  if(window.location.href == "https://ethan-scott.github.io/cs185_react/videos"){
    return(<Videos/>)
  }
  if(window.location.href == "https://ethan-scott.github.io/cs185_react/links"){
    return(<Links/>)
  }

}

export default App;